<template>
	<view class="content">
		<customHeader :title="pageName" :showBack="true" :headHeight="headerHeight"></customHeader>
		<view class="cont" :style="{marginTop:marginTop+'px'}">
			<view class="box" :style="{top:marginTop+10+'px',background:list.length==0?'#fff':'#F3F3F5'}">
				<scroll-view :scroll-top="scrollTop" scroll-y="true" class="scroll-Y" :style="{height:scrollHeight+'px'}"
					@scrolltolower="lower">
					<view class="item" v-for="(item,index) in list" :key="index">
						<view class="line flex flex-start">
							<view class="tit">姓名：</view>
							<view class="text">{{item.worker_name}}</view>
						</view>
						<view class="line flex flex_btween">
							<view class="in_item flex flex-start">
								<view class="tit">工作时长：</view>
								<view class="text">{{item.working_time}}</view>
							</view>
							<view class="in_item flex flex-start">
								<view class="tit">本期佣金：</view>
								<view class="text">{{item.commission_amount}}</view>
							</view>
						</view>
					</view>
					<view class="empty" v-if="list.length == 0">
						<image :src="imgUrl+'/broker/empty.png'" mode="heightFix"></image>
					</view>
				</scroll-view>
			</view>
		</view>
	</view>
</template>

<script>
	import customHeader from "@/components/custom_header.vue"
	const app = getApp()
	export default {
		data() {
			return {
				imgUrl:app.globalData.baseImageUrl,
				currentTab:0,
				pageName:"",
				marginTop:0,
				comValue:"",
				startTime:"",
				endTime:"",
				list:[],
				scrollHeight:0,
				headerHeight:0,
				maskInnerTop:0,
				id:"",
				type:"",
				currentPage:1,
				pagination:{}
			}
		},
		components:{
			customHeader
		},
		onPullDownRefresh() {
			this.currentPage = 1
			this.list = []
			this.getList()
		},
		onLoad(param) {
			this.id = param.id
			this.type = param.type
			if(param.type == "income"){
				this.pageName = "佣金收入详情"
			}else{
				this.pageName = "佣金支出详情"
			}
			let _this = this
			this.getMarginTop()
			setTimeout(function(){
				_this.getElementInfo()
				_this.getTopHeight()
			},500)
			this.getList()
		},
		methods: {
			
			getMarginTop(e){
				let menuButtonInfo = uni.getMenuButtonBoundingClientRect()
				this.marginTop = menuButtonInfo.height + menuButtonInfo.top
				this.maskInnerTop = this.marginTop + 20
			},
			getTopHeight(){
				let percent = 200/844
				let systemInfo = uni.getSystemInfoSync()
				this.headerHeight =Math.floor(systemInfo.screenHeight * percent) 
			},
			getElementInfo(){
				let _this = this
				let topHeight=0;
				let systemInfo = uni.getSystemInfoSync()
				let tabbarHeight =Math.floor(systemInfo.screenHeight * (46/844)) 
				console.log(tabbarHeight,"tabbar")
				_this.scrollHeight = systemInfo.screenHeight - _this.marginTop - tabbarHeight - 5
			},
			getList(){
				let url = "/broker/mini/commission/"+this.type+"/detail/"+this.id+"/items?page="+this.currentPage
				this.$request(url).then(res => {
					if(res.code == 0){
						this.list = this.list.concat(res.data.list)
						this.pagination = res.data.pagination
					}else{
						uni.showModal({
							title:res.msg
						})
					}
				})
			},
			lower(){
				if(this.list.length < this.pagination.totalCount){
					this.currentPage++
					this.getList()
				}else{
					if(this.list.length >= this.pagination.totalCount){
						uni.showToast({
							title:"已经到底啦~",
							icon:"none",
							duration:2000
						})
					}
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
::v-deep{
	.uni-select{
		border:none;
		background: #fff;
		height:88rpx;
		font-size: 28rpx;
		border-radius: 8rpx;
		padding:0 40rpx;
	}
	.uni-icons{
		display: none;
	}
	.uni-select__input-placeholder{
		font-size: 28rpx;
	}
	.uni-date-x--border {
	    height: 88rpx;
	    line-height: 88rpx;
	    padding: 0 40rpx;
	    background: #fff;
	    border-radius: 8rpx;
	    box-sizing: border-box;
		border:none;
	}
	.uni-datetime-picker--btn,.uni-calendar-item__weeks-box .uni-calendar-item--checked{
		background: #F7BC05;
	}
	.uni-datetime-picker-btn-text{
		color: #F7BC05;
	}
}
.top_area{
	width:calc(100% - 96rpx);
	position: fixed;
	left:48rpx;
	.sele_box{
		margin:0 20rpx 20rpx 0;
		.text{
			font-size: 28rpx;
			color:#fff;
			font-weight: bold;
		}
		image{
			width:34rpx;
			margin-right: 8rpx;
		}
	}
}
.box{
	position: fixed;
	width: calc(100% - 96rpx);
	left:48rpx;
	z-index:3;
	border-top-left-radius: 16rpx;
	border-top-right-radius: 16rpx;
	overflow: hidden;
	.item{
		padding:40rpx;
		border-radius: 16rpx;
		background: #fff;
		box-shadow: 0 2rpx 6rpx 0 rgba(0, 0, 0, 0.15);
		margin-bottom: 20rpx;
		position: relative;
		.line{
			margin-bottom: 28rpx;
			&:last-child{
				margin-bottom: 0;
			}
			font-size:28rpx;
			.tit{
				color:#686868;
			}
			.text{
				color:#333;
			}
		}
	}
}
</style>
